.mani_wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;padding:10px;
}
.moves{
  background-color: aliceblue;
  color: #000;
  border: 1px solid gray;
  padding: 3px;
  font-weight: bold;
  text-align: start;
  margin:10px;
  width:20%;
  height:320px;
}
.moves img{width:100%;height:250px;object-fit: cover;}
.moves h1{font-size:15px;}
.App {
  text-align: center;
}

.boxed{
display: flex;
border:1px solid #555;
margin: 5px;
padding: 10px; 
flex-direction:row;
justify-content: center;
flex-wrap: wrap;
}
.itme{
  border: 2px solid #333;
  padding:10px;
  margin: 10px;
  width:500px;
}
.Data_Table{
  width:80%;margin: auto;
  border:1px solid #000;
  border-collapse: collapse;
 
  margin-top: 30px;
}
.Data_Table tr,td,th{border:1px solid #000; padding:10px 2px;}


.data{margin-top:15px;width:60%;margin: auto;display: flex;justify-content: space-between;}

@media(max-width:767px){
  .moves{width:100%;}
  .boxed{flex-direction: column;}
}

.jyGdND .input-block{text-align: start;}